.nav-sub-menu {
    display: none;
    position: absolute;
    top: 20px;
    left: 0;
}

.nav-sub-menu li a {
    padding-bottom: 0;
}

.site-nav-right li {
    position: relative;
}

.site-nav-right li:hover > .nav-sub-menu {
    display: block;
}

.site-nav-left li {
    position: relative;
}

.site-nav-left li:hover > a {
    text-decoration: underline;
}

.site-nav-left li:hover > .nav-sub-menu {
    display: block;
}

.site-header-black .nav-sub-menu {
    background-color: #070809;
    top: 35px;
}

.site-header-black .nav-sub-menu li:last-child a {
    padding-bottom: 10px
}

.site-header-black .nav-sub-menu li a {
    color: white;
}